body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', 'Segoe UI', Arial, sans-serif;
    background-color: #1a1a1a; /* Тёмный фон для атмосферы выживания */
    color: #e0e0e0; /* Светлый текст для контраста */
  }
  .menu-card {
    width: calc(46% - 10px); /* Вернём чуть увеличенный размер */
    max-width: 330px; /* Ограничение максимальной ширины */
    height: 170px; /* Высота вернулась к предыдущему значению */
    margin: 5px; /* Отступы вокруг карточки */
    background-color: #3c3c3c; /* Серо-металлический фон карточек */
    border-radius: 10px; /* Округлённые углы для визуального баланса */
    border: 1px solid #5a5a5a; /* Металлическая рамка */
    box-shadow: 0 5px 9px rgba(0, 0, 0, 0.2); /* Лёгкая тень */
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  .menu-card img {
    width: 100%; /* Изображение растягивается по ширине карточки */
    height: auto; /* Высота автоматически подстраивается под пропорции изображения */
    object-fit: contain; /* Показывает всё изображение внутри карточки, добавляя отступы, если нужно */
  }
  
  
  .menu-card:hover {
    transform: scale(1.05); /* Умеренное увеличение при наведении */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Усиленная тень при наведении */
  }
  
  @media screen and (max-width: 768px) {
    .menu-card {
      width: calc(100% - 10px); /* Увеличение карточки для мобильных устройств */
      max-width: none;
      height: 180px; /* Высота для мобильных устройств */
    }
  }
  