body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Segoe UI', Arial, sans-serif; /* Удобочитаемый шрифт */
  background-color: #121212; /* Темный фон для общей тематики */
  color: #e0e0e0; /* Светлый текст для контраста */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Courier New', Consolas, 'Fira Code', monospace; /* Современный стиль шрифта для кода */
  background-color: #2b2b2b; /* Темный фон для выделения кода */
  color: #ffffff; /* Белый текст */
  padding: 4px 8px; /* Отступы для удобочитаемости */
  border-radius: 4px; /* Закругленные углы */
}
